import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Class } from '../class';
import { SendPostService } from '../send-post.service';
import { Router, ActivatedRoute, Params } from '@angular/router';

declare var $: any;
@Component({
  selector: 'app-qr-form',
  templateUrl: './qr-form.component.html',
  styleUrls: ['./qr-form.component.css']
})
export class QrFormComponent implements OnInit {
  buttonDisabled = false;
  id;
  model: any = {};
  message;
  dataa;
  data;
  submitsuc = true;
  loader = false;
  error;
  result;
  idnew: any = {};
  state: any = [];
  countries;
  name:any;
  stval: any;
  ne:any;
  constructor(
    private activatedRoute: ActivatedRoute,
    private http: HttpClient,
    private router: Router,
    private serv: SendPostService,
  ) {
    localStorage.setItem('auth', "false");
    this.activatedRoute.params.subscribe(paramsId => {
      this.id = paramsId.id;
      sessionStorage.setItem("qr", this.id);
      if (this.id == undefined) {
        this.buttonDisabled = true;
        this.submitsuc = false;
        this.error = 'It is Not Valid URL.  Try Again!';
      }
    });


  }



  onSubmit(f) {
    this.model.code = this.id;
    this.loader = true;
    this.dataa = JSON.stringify(this.model);
    //alert(this.dataa);  
    this.serv.senddata(this.dataa).subscribe(
      (res) => {
       // console.log(res);
        this.data = res;
        this.loader = false;
        sessionStorage.setItem("imgpath", this.data.message.gift_img);
        sessionStorage.setItem("imgid", this.data.message.id);
        sessionStorage.setItem("Bcode", this.data.data.code);


        this.router.navigateByUrl('master');
        f.resetForm();
        this.submitsuc = false;
        localStorage.setItem('auth', "true");
      },
      error => {
        this.error = error;
        this.loader = false;
        this.submitsuc = false;
      }
    );
  }


  ngOnInit() {

    this.serv.getstate().subscribe(
      pdata => {
        this.state = pdata
       // console.log(this.state);
      }
    )
    var a = sessionStorage.getItem("qr");
    this.serv.getname(a).subscribe(
      pdata => {
        this.name = pdata;
        this.ne = this.name.data.name;
      }
    )

    

    this.idnew = JSON.stringify({ "code": this.id });
  }
}
