import { Injectable } from '@angular/core';
import {Class} from './class';
import { HttpClient , HttpHeaders , HttpErrorResponse} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
  
@Injectable({
  providedIn: 'root'
})
export class SendPostService {

  constructor( public http:HttpClient) {
   
   }
   httpOptions = {
    headers: new HttpHeaders({'Content-Type': 'application/json'  }), };

    private handleError(error: HttpErrorResponse) {
      let errortxt;
      if (error.error instanceof ErrorEvent) {
        // A client-side or network error occurred. Handle it accordingly.
       //console.error(error.message);
        errortxt = error.message;     
      } else {
        // The backend returned an unsuccessful response code.
        // The response body may contain clues as to what went wrong,
        //console.error(error);
        errortxt = error.error.message;     
        
      }
      // return an observable with a user-facing error message
      return throwError(errortxt);
    };

  serverpath = 'https://www.magiklights.com/qrcode';
  localpath = 'http://192.168.1.19/magikqrcode';
  
  url = `${this.serverpath}/backend/public/api/conatct`;  
  check = `${this.serverpath}/backend/public/api/check`;
  state = `${this.serverpath}/backend/public/api/state`;
  termcon = `${this.serverpath}/backend/public/api/term`;
  name = `${this.serverpath}/backend/public/api/getcampaign?code=`;
  dummy = 'https://jsonplaceholder.typicode.com/posts';
  
  senddata(data:Class){
    return this.http.post<Class>(this.url, data , this.httpOptions)
    .pipe(      catchError(this.handleError)    )
  }; 
  checkdata(s:Class){
    return this.http.post<Class>(this.check , s , this.httpOptions)
    .pipe(      catchError(this.handleError)    )
  };
  getstate(){      
    return this.http.get<Class>(this.state)   
  }
  gettermcond(){
    return this.http.get(this.termcon)
  }
  getname(code){
    return this.http.get(this.name + code)
  }
}
